body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
.mapUI {
    height: 100vh;
    z-index: 1;
}

.T2 {
    stroke: red;
    z-index: 3;
}

.T1 {
    z-index: 5;
}
#subtitle {
    color:white;
    text-align: center;
    font-size: 20px;
    margin-bottom: 0;
    padding-bottom: 10px;
}

#sub_info {
    color: white;
    font-size: 12px;
    margin-top: 6px;
    margin-bottom: 6px;
    text-align: center;

}

#delete {
  font-size: 20px;
}

#sortable_layers {
    color: white;
    list-style-type: none;
    padding: 0;
    font-size: 10px;
  }

#sortable_layers li {
    margin: 0 3px 3px 3px;
    padding: 0.4em;
    padding-left: 0.4em;
    font-size: 1.4em;
    height: 18px;
    cursor: move;
}

#sortable li.selected {
    background-color: #535353;
    color: white;
}

.layer{
    background-color: #535353;
}

.active{
    background-color: #6D7D92;
    border: 1px solid #c5c5c5;
    font-weight: normal;
}

#upload {
    width:100px;
    margin: -20px -50px;
    position:relative;
    top:50%;
    left:50%;
}




#sidebar_title_div {
    width: 100%;
    height: 30px;
    padding-top: 0;
  }

.option {width: 100% !important;}

#title {
    padding: 6px 8px 6px 16px;
    margin-top: -17px;
    margin-left: 5px;
    text-decoration: underline;
    font-size: 25px;
    color: white;
    display: block;
  }

  .buffer{
    background-color: #4d4fca;
  }

  .buffer_content{
    margin-bottom: 55px !important;
  }

  .dissolve{
    background-color: #e0b84b;
  }

  .union{
    background-color: purple;
  }

  .union_content{
    margin-bottom: 90px !important;
  }

  .intersect{
    background-color: green;
  }

  .intersect_content{
    margin-bottom: 90px !important;
  }

  .difference{
    background-color: red;
  }

  .difference_content{
    margin-bottom: 90px !important;
  }

  .extract {
    color: black;
    background-color: white;
  }



#tool_layer {
  color: white;
  list-style-type: none;
  padding: 0;
  font-size: 10px;
}


#tool_layer li {
  margin: 0 3px 3px 3px;
  padding: 0.4em;
  padding-left: 0.4em;
  font-size: 1.4em;
  height: 18px;
  cursor: pointer;
}



#tool_title {
  font-size: 18px;
  color: rgb(53, 41, 41);
}

#subtitle {
  color:white;
  text-align: center;
  font-size: 20px;
}

#apply_buffer{
    margin-left: 48px;
    margin-top: 7px;
}


#tool_title{
    color: white;
    text-align: center;
    font-size: 16px;
}

#apply_dissolve{
    margin-left: 48px;
    margin-top: 0;
}
#map {
  height: 100vh;
  margin-right: 165px;
}

#sidebar {
  height: 100%;
  width: 165px;
  position: fixed;
  z-index: 1;
  top: 0;
  right: 0;
  background-color: #222222;
  overflow-x: hidden;
  padding-top: 20px;
}
